import {BlogDefinition, BlogPaths, BlogTags, codeBlockOf, urlOf} from "@/assets/blogs/BlogDefinition";
import {BlogContentTypes} from "@/util/BlogContentUtil";

const createDate = new Date(2023, 6, 10);
const updateDate = new Date(2023, 6, 10);

const content = `
The original question is: ${urlOf("https://codeforces.com/contest/1838/problem/E", "E. Count Subsequences")}.

I didn't solve this question. I got the transition state, but didn't catch the essential step.

The transition state is easy:

Let $dp_{i, j}$ means for at b's $i$, there are at most first $j$ items of $a$ fitting in $b$ ($a_{j + 1}$ isn't in).

Then,

$$
dp_{i, j} = \\begin{cases}
dp_{i - 1, j - 1} + (k-1) dp_{i - 1, j}, j < n \\\\
dp_{i - 1, j - 1} + k dp_{i - 1, j}, j = n
\\end{cases}
$$ 

What I did after that was I concluded that, if $j < n$, $dp_{i, j} = \\binom{i}{j} (k-1)^{i - j}$.

Then, I derived

$$
\\begin{array}{l}
dp_{m, n} = \\sum^{m-1}_{i=n-1} (k-1)^{i - (n - 1)} k^{m - 1 - i} \\binom{i}{n-1} \\\\
= k^{m - n} \\sum^{m-1}_{i=n-1} (1 - \\dfrac{1}{k})^i \\binom{i}{n-1}, \\text{let } q = m - 1, p = n - 1, d = (1 - \\dfrac{1}{k}) \\\\
= k^{q - p} \\sum^{q}_{i = p} d^i \\binom{i}{p} 
\\end{array} 
$$

Then I tried to conclude a way to find $k^{q - p} \\sum^{q}_{i = p} d^i \\binom{i}{p}$ in $O(N \\log(N))$.

But the correct way is, to observe that, the whole solution is independent with the elements in array $a$, so, just assume $a_i \\equiv 1$.

We then, only need to conclude, how many sequences are there such that has less than $n$ $1$s. 

The solution is clearly

$$
k^m - \\sum^{n-1}_{i=0} \\binom{m}{i} k^{m - i} = k^m - \\sum^{n-1}_{i = 0} \\dfrac{m^{(i)}}{i!} k^{m-i}
$$

This can be calculated in $O(N\\log(N))$.

### Other

Notice that, this also says that:

$$
\\sum^{m}_{i=n} (k-1)^{i - n} k^{m - i} \\binom{i}{n} =  k^{m+1} - \\sum^{n}_{i = 0} \\binom{m+1}{i} k^{m-i+1}
$$

Or:

$$
\\sum^{n}_{i = 0} \\binom{m+1}{i} k^{m-i+1} + \\sum^{m}_{i=n} \\binom{i}{n} (k-1)^{i - n} k^{m - i} =  k^{m+1}
$$

This could be an important identity because notice the LHS is binomial + hockey stick and RHS is a power.

`

export const CodeforcesCountSubsequences = new BlogDefinition({
    title: "Codeforce Round 877 E: Count Subsequences",
    paths: BlogPaths.Codeforces,
    content: content,
    contentType: BlogContentTypes.Markdown,
    tags: [BlogTags.Algorithms, BlogTags.Codeforces],
    createDate: createDate,
    updateDate: updateDate
});
