<template>
  <div class="notes"
       :style="{
         'padding-top': this.$store.state.blogPaddingTop,
         'padding-bottom': this.$store.state.mainContainerPaddingBottom
       }">
    <div>
      <div v-for="grp in noteGroups">
        <div class="note-group-title">
          {{ grp[0] }}
        </div>
        <div v-for="note in grp[1]" class="note-link">
          <router-link
              custom
              :to="`/note/${note.id}/${note.urlTitle}`"
              v-slot="{href, route, navigate, isActive, isExactActive}">
            <el-link :href="href" @click="navigate" style="font-size: 18px;" :underline="false">
              {{ note.title }}
            </el-link>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as MarkdownIt from 'markdown-it';
import {BlogContentTypes} from "@/util/BlogContentUtil";
import Blog from "@/views/main/Blog";
import {ref} from "vue";
import {notes} from "@/assets/Notes";

const markdown = new MarkdownIt();

export default {
  name: "Notes",
  components: {Blog},
  setup() {
    const notesByCategory = ref((() => {
      let dict = {};
      for (let note of notes) {
        let cate = note.category;
        if (dict[cate] === undefined) {
          dict[cate] = [];
        }
        dict[cate].push(note);
      }
      let arr = [];
      for (let [key, value] of Object.entries(dict)) {
        arr.push([key, value]);
      }
      arr.sort((x, y) => x[0] < y[0]);
      return arr;
    })());

    return {
      noteGroups: notesByCategory
    }
  }
}
</script>

<style scoped>
.notes {
  margin-left: 5%;
  margin-right: 5%;
  text-align: left;
}

.note-group-title {
  font-size: 150%;
}

.note-link {
  margin-top: 2%;
  margin-bottom: 2%;
  text-decoration: underline;
}
</style>