const content = `

Questions as examples:

1. ${urlOf("http://acm.student.cs.uwaterloo.ca/acm/problems/statements.pdf", "ICPC Waterloo 2023 Winter Local D")}
2. ${urlOf("https://cemc.uwaterloo.ca/contests/computing/past_ccc_contests/2023/ccc/seniorEF.pdf", "CCC 23 S4")}
3. ${urlOf("https://leetcode.com/problems/modify-graph-edge-weights/", "Modify Graph Edge Weights")}

## Basic Idea

Assume we have a graph constructing by edges $G = e_1, e_2, ..., e_k$. And we have a validation function:

$$f(G) = [\\forall q \\in \\text{path of } G, P(q)]$$

where $P$ is a function based on a path.

Then, we will know the property that, if $f(G) = 0, f(G \\cup \\{e'\\}) = 1$, then $P(q) = 0 \\implies e' \\in q$.

Proof: If $e' \\not \\in q$, then $q \\in \\text{path of } G, f(G) = 0$, contradict.

Besides, because $f(G \\cup \\{e'\\}) = 1$, we know the existence of $q$.

Therefore, we can add $e$ into graph one by one, assume $e$ is in the path $q$. Then adjust on $e$ such that $q$ would satisfy some special condition.

The template of these type of questions could be:

Provides a subgraph $G'$ satisfying $f(G)$.

## ICPC Waterloo 2023 Winter Local D

Clearly, the function $P$ is if there exist a cycle path on $G$. And we understand that $f(G)$ iff there exists a cycle. Also, we want the cost of $G'$ be minimum.

Notice that edge is exponentially increasing. So, we would choose edge $i$ if and only if choosing all edges $0...i - 1$ won't work.

So, we sort it by edge cost, then one by one adding it into the graph.

When adding $e_i$, we know $\\{e_0, e_1, ..., e_{i-1}\\}$. But if adding $e_i$ work, then it means $e_i$ is in the cycle path. So, when we are doing a bfs on $e_{i_0}$, we are able to reach $e_{i_1}$ without using the edge $e_i$. We can use a DSU to optimize it.

## CCC 23 S4

This question isn't exactly the same. But it uses the similar idea. The question uses the proposition that "an edge $(u, v, w)$ is on a shortest path in $G$ if and only if $w = dis[u, v]$".

Proof:

$L \\implies R$: If $e = (u, v, w)$ is on arbitrary shortest path in $G$, then $w$ should be $dis[u, v]$, otherwise replace it with a path from $u$ to $v$ with $dis[u, v]$, it is even shorter.

$R \\implies L$: If $e = (u, v, w)$ having $w = dis[u, v]$, then $e$ can be on the shortest path from $u$ to $v$.

So, we sort all edges by $w$. Keep adding $e$ into graph $G$. When we reach $e$, we know it should be in graph only if $dis[u, v] = w$.

However, even if $dis[u, v] = w$, we might not add it into the graph. So we proceed induction here.

Maintain another graph $H$. We know we must insert the smallest edge $(w, c)$ into the graph.

Then, $H_{i-1}$ is the edges we must insert into graph at $e_1 .. e_{i - 1}$.

1. $e_i$ exists on shortest path if and only if $dis[u, v] = w$. If $dis_H[u, v] = w$, then all paths formed by $e_i$ in $G'$, can be replaced with the one in $H$, so we won't choose it. So we only choose when $dis_H[u, v] > w$.
2. If $e_i$ is necessary for $H_{i-1}$, then no matter how to add $e_{i + 1}$ to $e_m$, $e_i$ is still necessary for $H_{i-1} \\cup S, S \\subseteq \\{e_{i + 1}, ..., e_m\\}$.
3. This would work if $w$ are positive. But here, $w$ can be $0$. So when we are adding edges, we can sometimes using a $0$ edge with another $e_{i + 1}$ edge such that $w_{i + 1} = w_i$ to still perform same as $e_i$, but $c_{i + 1}$ can be smaller than $c_i$.
4. So, to deal with this problem, we set $c$ is increasing after $w$. Then, if $e_i$ is necessary for $H_{i - 1} \\cup S$, while $dis_{H_{i - 1} \\cup S}[u_i, v_i] = w_i$, we know it concat some $e_{i + 1}$ having $w_{i + 1} = w_i$. Then, because $c_{i + 1} \\geq c_i$, $w_j = 0$ edges has been visited already, replacing $e_{i + 1}$ by $e_i$ doesn't change the shortest paths, but decreases $c$. So we still choose $e_i$.
5. The proof of (4) can be considered as, if $dis_{H_{i - 1} \\cup S}[u_i, w_i] = w_i$, then $e_{i+1} \\in S$. But $(u_{i + 1}, w_{i + 1}) \\neq (u_i, w_i)$. So, $dis[u_i, w_i]$ is on the shortest path only because there exists $w = 0$ edges connecting. Then, we can prove that, no matter using $e_{i + 1}$ or $e_i$, all vertex's distance are the same. By contradiction, if in one of them it is larger than the other, but all vertex's in this case they are sharing $w=0$ edges, they should be the same.   

So, $e_i$ would be necessary iff $dis_H[u, v] > w$.

## Modify Graph Edge Weights

In this case, our $P(q)$ is if $q$ is a path from $s$ to $d$ with distance $targ$.

Then we make the edges with negative distance as $w = 1$. Add it into graph $G$, forming $G'$.

If $G'$ has $dis_{G'}[s, d] > targ$, then doesn't matter.

If it is $< targ$, then it means the edge with distance $1$ is in the shortest path. So, we add $1$ by $targ - dis$. Now, we know shortest path at most will be $targ$. However, if shortest path is not $targ$, then shortest path is $< targ$. Then we know the added edge isn't visited (otherwise it would be targ exactly). So original $G$ has a path $< targ$, contradict.

So, we only need to change the $w$ to $targ - dis$. 
 
`

